*,*::before,*::after{
    box-sizing: border-box;
}

img{
    max-width: 100%;
    height: auto;
}

.recommend{
    height: 791px;
    h2{
        color: #650300;
        font-family: Noto Sans TC;
        font-size: 48px;
        line-height: 72px;
        font-weight: bold;
        text-align: center;
        padding-top: 80px;
        padding-bottom: 48px;
    }
}

.customer_opinion{
    display: flex;
    max-width: 1296px;
    justify-content: space-between;
}

.opinion{
    max-width: 306px;
    border-radius: 0 0 4px 4px;
    box-shadow: 0px 2px 6px #00000029;
    img{
        max-width: 100%;
        height: auto;
        margin-bottom: 16px;
        border-radius: 4px 4px 0 0;
    }    
    h3{
        color: #000000;
        font-family: Noto Sans TC;
        font-size: 16px;
        line-height: 24px;
        padding-left: 16px;
        padding-bottom: 8px;
    }
    p{
        color: #707070;
        font-family: Noto Sans TC;
        font-size: 14px;
        line-height: 21px;
        padding-left: 16px;
        padding-top: 64px;
        padding-bottom: 16px;
    }
}

.contact{
    background-color: #FBF2F2;
    padding-bottom: 80px;
    h2{
        color: #650300;
        font-family: Noto Sans TC;
        font-size: 48px;
        line-height: 72px;
        font-weight: bold;
        text-align: center;
        padding-top: 80px;
        padding-bottom: 48px;
    }
    h3{
        color: #000000;
        font-family: Noto Sans TC;
        font-size: 20px;
        line-height: 30px;
        padding-bottom: 24px;
        max-width: 636px;
    }
    h4{
        color: #000000;
        font-family: Noto Sans TC;
        font-size: 16px;
        line-height: 24px;
        max-width: 636px;
        padding-bottom: 8px;
    }
}

.contact_name_placeholder{
    max-width: 636px;
    border: 1px solid #000000;
    margin-bottom: 16px;
    height: 34px;
}

.contact_phone_placeholder{
    max-width: 636px;
    border: 1px solid #000000;
    margin-bottom: 16px;
    height: 34px;
}

.contact_email_placeholder{
    max-width: 636px;
    border: 1px solid #000000;
    margin-bottom: 16px;
    height: 34px;
}

.contact_opinion_placeholder{
    max-width: 636px;
    border: 1px solid #000000;
    margin-bottom: 16px;
    height: 134px;
}

.contact p{
    color: #000000;
    font-family: Noto Sans TC;
    font-size: 16px;
    line-height: 24px;
    opacity: 0.2;
    padding-left: 12px;
    padding-top: 6px;
}

.contact_check{
    max-width: 636px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    img{
        width: 24px;
    }
    h5{
        color: #000000;
        font-family: Noto Sans TC;
        font-size: 16px;
        line-height: 24px;
        max-width: 636px;
        padding-left: 4px;
    }
}

.buttom_send{
    max-width: 416px;
    background-color: #AA0601;
    padding-top: 16px;
    padding-bottom: 16px;
    margin-top: 40px;
    h6{
        color: #FFFFFF;
        font-family: Noto Sans TC;
        text-align: center;
        font-size: 16px;
        line-height: 24px;
    }
}